/* Import Montserrat font from Google Fonts */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;600;700&display=swap');

/* Update font-family for body and HTML */
body, html {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif; /* Change to Montserrat */
  background-color: #f4f4f4;
}

/* The rest of your existing CSS remains the same */
.App {
  text-align: center;
}

.header-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #ffffff;
  border-bottom: 1px solid #dee2e6;
}

.logo {
  font-size: 1.5rem;
  font-weight: bold;
  color: #2a3037;
  font-family: cursive;
}

.user-info {
  display: flex;
  align-items: center;
}

.user-info span {
  margin-left: 10px;
  font-size: 1.2rem;
}

.header-bottom {
  background-color: #f8f9fa;
  padding: 10px 0;
}

.navbar {
  background-color: transparent;
}

.navbar ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
}

.navbar li {
  margin: 0 10px;
}

.navbar a {
  text-decoration: none;
  color: #343a40;
  padding: 10px 20px;
  display: block;
  border-radius: 5px;
  font-weight: 400;
}

.navbar a:hover {
  background-color: #e9ecef;
}

.container {
  padding: 20px;
}
